@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: 'Lato', sans-serif;
  font-size: 20px;
}
div.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;

  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
  background-color: #ddd;
  color: #222;
}

.header {
  flex-basis: 30vh;
}

.main {
  padding: 30px 0;
  flex-grow: 1;

  display: flex;
}

.main .aside {
  min-width: 240px;
}

.main .page {
  flex-grow: 1;
}

.footer {
  background-color: black;
  color: white;
  padding: 10px 0;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 2px;
}
.header {
  position: relative;
  overflow: hidden;
}

.header img {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  min-width: 100%;
  min-height: 100%;
}
nav.main-nav {
  padding: 0 30px 30px 0;
}

.main-nav ul {
  list-style-type: none;
}

nav.main-nav a {
  display: block;
  text-decoration: none;
  color: black;
  text-transform: uppercase;
  font-weight: bold;
  padding: 10px;
  -webkit-transition: .2s;
  transition: .2s;
}

nav.main-nav a:hover {
  -webkit-transform: translateX(20px);
          transform: translateX(20px);
}

nav.main-nav a.active {
  -webkit-transform: translateX(0);
          transform: translateX(0);
  border-left: 10px solid grey;
}

nav.main-nav li:nth-child(1) a.active {
  border-left-color: green;
}

nav.main-nav li:nth-child(2) a.active {
  border-left-color: blue;
}

nav.main-nav li:nth-child(3) a.active {
  border-left-color: orange;
}

nav.main-nav li:nth-child(4) a.active {
  border-left-color: red;
}
